body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
* {
  font-family: 'Lato', sans-serif;
}
a {
  text-decoration: none;
  cursor: pointer;
}
button {
  width: 96px;
  height: 48px;
  border: 0;
  cursor: pointer;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
